import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';

import { Card, Row, message, Col } from 'antd';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { getYearEducations, deleteYearEducation } from 'services/yearEducationService';
import './styles.scss';

class ListYearEducationPage extends Component {
  state = { reload: 0 };
  handleDelete = async id => {
    await deleteYearEducation(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const { reload } = this.state;
    const columns = [
      {
        title: 'Từ năm',
        dataIndex: 'fromYear',
        key: 'fromYear',
      },
      {
        title: 'Đến năm',
        dataIndex: 'toYear',
        key: 'toYear',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#YearEducation/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Khu vực" className="yearEducation">
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <BtnAddNew href="#YearEducation/form/new" />
            </CartHeadAction>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData reload={reload} service={getYearEducations} columns={columns} rowKey="id" />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListYearEducationPage />
  </Page>
);



